import productList from '@/components/productList.vue';
import interfaces from '@/utils/interfaces.js';
import { mapState, mapMutations } from 'vuex';
export default {
	computed: mapState(['hasLogin', 'userinfo']),
	components: {
		productList
	},
	data() {
		return {
			user_id: '',
			collect_type:1,
			user_bank: {},
			user_company: {
				transparencyData: [],
				qualificationsData: []
			},
			flag: true,
			indicatorDots: true,
			autoplay: true,
			interval: 2000,
			duration: 500,
			filterByList: [
				{
					cntitle: '需求中心',
					entitle: 'Recommend',
					selected: true
				},
				{
					cntitle: '采购订单',
					entitle: 'Purchase',
					selected: false
				},
				{
					cntitle: '销售订单',
					entitle: 'Sale',
					selected: false
				},
				{
					cntitle: '委托订单',
					entitle: 'Entrust',
					selected: false
				}
			],
			goodsList: [
				{
					id: 1,
					img: '/static/images/product.png',
					name: '这款呼吸机 卖疯了这款呼吸机 卖疯了',
					price: '2000'
				},
				{
					id: 2,
					img: '/static/images/product.png',
					name: '这款呼吸机 卖疯了这款呼吸机 卖疯了',
					price: '2000'
				},
				{
					id: 3,
					img: '/static/images/product.png',
					name: '这款呼吸机 卖疯了这款呼吸机 卖疯了',
					price: '2000'
				},
				{
					id: 4,
					img: '',
					name: '这款呼吸机 卖疯了这款呼吸机 卖疯了',
					price: '2000'
				},
				{
					id: 5,
					img: '/static/images/product.png',
					name: '这款呼吸机 卖疯了这款呼吸机 卖疯了',
					price: '2000'
				}
			]
		};
	},
	onLoad(options) {
		this.user_id = options.userid;
		this.getEnterprise();
	},
	methods: {
		collect() {
			this.flag = !this.flag;
			//console.log(this.flag);
			this.request({
				url: interfaces.getCollectData,
				dataType: 'JSON',
				method: 'POST', //请求方式
				data: {
					data:{
						collect_id: this.user_id,
						collect_type:this.collect_type
					}
				},
				success: res => {
					console.log(res,323)
					uni.showToast({
						title: '收藏成功',
						duration: 2000
					});
				
				}
			})
		},
		handleSelect(index) {
			this.filterByList[index].selected = true;

			// 其他的selected false
			for (let i = 0; i < this.filterByList.length; i++) {
				if (i != index) {
					this.filterByList[i].selected = false;
				}
			}
		},
		getEnterprise() {
			this.request({
				url: interfaces.getEnterpriseData,
				dataType: 'JSON',
				method: 'POST', //请求方式
				data: {
					data: {
						user_id: this.user_id
					}
				},
				success: res => {
					console.log(res, 1212);
					this.user_bank = res.data.profiles.user_bank;
					this.user_company = res.data.profiles.user_company;
					this.user_company.transparencyData = this.user_company.company_transparency.split(',');
					this.user_company.qualificationsData = this.user_company.qualifications.split(',');
				}
			});
		}
	}
};
